.add-cell {
	position: relative;
	opacity: 0;
	transition: opacity 0.3s ease-in 0.2s;
	margin: 12px 0;
}
.add-cell:hover {
	opacity: 1;
}
.add-cell.force-visible {
	opacity: 1;
}
.add-cell .divider {
	position: absolute;
	top: 50%;
	bottom: 50%;
	border-bottom: 1px dashed lightslategray;
	left: 20%;
	right: 20%;
	width: 60%;
	z-index: -1;
}
.add-cell .add-buttons {
	display: flex;
	justify-content: center;
}
.add-cell .add-buttons button {
	margin: 0 10rem;
}
